body {
  background-color: #e2dec2;
}

h3 {
  margin: 4px;
  color: white;
}

.App {
  height: 100vh;
}

.menuBar {
  background-color: #fff5b1;
  height: 30px;
  padding: 8px;
  display: grid;
  grid-template-columns: 80px auto 80px;
  justify-content: space-between;
  align-items: center;
}

.menuOpcion {
  display: flex;
  gap: 8px;
}

.panel {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.table {
  background-color: #b0b0b091;
  margin: auto;
}

.tableRow {
  font-size: 10pt;
  display: grid;
  grid-template-columns: 120px repeat(2, 100px);
  grid-auto-rows: 24px;
  align-items: center;
  border-collapse: collapse;
  border: 1px solid black;
}

.labelItem {
  border: 1px solid black;
  height: 100%;
  display: flex;
  align-items: center;
  padding-left: 8px;
}

.checkBox {
  border: 1px solid black;
  height: 100%;
  display: flex;
  align-items: center;
  padding-left: 8px;
}

/***********************/

.addPlayerContainer {

  width: 100%;
  min-height: 100vh;
  background-color: #000000e6;
  position: absolute;
  display: flex;
}

.addPlayerPanel {
  background-color: #fff;
  width: 60%;
  height: 256px;
  margin: auto;
  padding: 16px;
  border-radius: 16px;
  display: grid;
  grid-template-rows: 64px auto 64px;
  grid-auto-columns: 100%;
  align-items: center;
  justify-items: center;
  justify-content: space-between;
}

.inputGroup {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 90%;
  gap: 8px;
  text-align: center;
}

.buttonGroup {
  display: flex;
  gap: 8px
}

.okButton {
  height: 32px;
  width: 90px;
}